export function convertEditSubHospital(e) {
    var array = []
    for (let [key, value] of Object.entries(e)) {
        array.push(
            {
                "name": [
                    key
                ],
                "value": value
            }
        )
    }
    var array1 = []
    if(Object.keys(e).length !== 0){
        for (let [key, value] of Object.entries(e.personInCharge)) {
            array.push(
                {
                    "name": [
                        key
                    ],
                    "value": value
                }
            )
        }
    }
    return [...array, ...array1]
}

export function keyMenu(e){
    const myArr = e.split("/");
    let x = ""

    if(myArr[1] === 'people' || myArr[1] === 'subPeople'){
        x="/people"
    }

    else if(myArr[1] === 'case' || myArr[1] === 'subCase'){
        x="/case"
    }

    else if(myArr[1] === 'quarantine' || myArr[1] === 'subQuarantine'){
        x="/quarantine"
    }

    else if(myArr[1] === 'hospital' || myArr[1] === 'subHospital'){
        x="/hospital"
    }

    else if(myArr[1] === 'specifylocation' || myArr[1] === 'subSpecifylocation'){
        x="/specifylocation"
    }

    else if(myArr[1] === 'interview' || myArr[1] === 'subInterview'){
        x="/interview"
    }

    else if(myArr[1] === 'sample'){
        x="/sample"
    }

    else if(myArr[1] === 'user'){
        x="/user"
    }

    else if(myArr[1] === 'reportDaily'){
        x="/reportDaily"
    }

    else if(myArr[1] === 'reportExcel'){
        x="/reportExcel"
    }

    else{
        x="/"
    }
    
    return x
}

